import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import qiyeList from '@/views/pages/qiye/list'
import qiyeDetail from '@/views/pages/qiye/formModel'
import qiyeAdd from '@/views/pages/qiye/formAdd'
import gangweileixingList from '@/views/pages/gangweileixing/list'
import gangweileixingDetail from '@/views/pages/gangweileixing/formModel'
import gangweileixingAdd from '@/views/pages/gangweileixing/formAdd'
import zhaopinxinxiList from '@/views/pages/zhaopinxinxi/list'
import zhaopinxinxiDetail from '@/views/pages/zhaopinxinxi/formModel'
import zhaopinxinxiAdd from '@/views/pages/zhaopinxinxi/formAdd'
import gangweishenqingList from '@/views/pages/gangweishenqing/list'
import gangweishenqingDetail from '@/views/pages/gangweishenqing/formModel'
import gangweishenqingAdd from '@/views/pages/gangweishenqing/formAdd'
import mianshitongzhiList from '@/views/pages/mianshitongzhi/list'
import mianshitongzhiDetail from '@/views/pages/mianshitongzhi/formModel'
import mianshitongzhiAdd from '@/views/pages/mianshitongzhi/formAdd'
import mianshijieguoList from '@/views/pages/mianshijieguo/list'
import mianshijieguoDetail from '@/views/pages/mianshijieguo/formModel'
import mianshijieguoAdd from '@/views/pages/mianshijieguo/formAdd'
import storeupList from '@/views/pages/storeup/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'qiyeList',
			component: qiyeList
		}, {
			path: 'qiyeDetail',
			component: qiyeDetail
		}, {
			path: 'qiyeAdd',
			component: qiyeAdd
		}
		, {
			path: 'gangweileixingList',
			component: gangweileixingList
		}, {
			path: 'gangweileixingDetail',
			component: gangweileixingDetail
		}, {
			path: 'gangweileixingAdd',
			component: gangweileixingAdd
		}
		, {
			path: 'zhaopinxinxiList',
			component: zhaopinxinxiList
		}, {
			path: 'zhaopinxinxiDetail',
			component: zhaopinxinxiDetail
		}, {
			path: 'zhaopinxinxiAdd',
			component: zhaopinxinxiAdd
		}
		, {
			path: 'gangweishenqingList',
			component: gangweishenqingList
		}, {
			path: 'gangweishenqingDetail',
			component: gangweishenqingDetail
		}, {
			path: 'gangweishenqingAdd',
			component: gangweishenqingAdd
		}
		, {
			path: 'mianshitongzhiList',
			component: mianshitongzhiList
		}, {
			path: 'mianshitongzhiDetail',
			component: mianshitongzhiDetail
		}, {
			path: 'mianshitongzhiAdd',
			component: mianshitongzhiAdd
		}
		, {
			path: 'mianshijieguoList',
			component: mianshijieguoList
		}, {
			path: 'mianshijieguoDetail',
			component: mianshijieguoDetail
		}, {
			path: 'mianshijieguoAdd',
			component: mianshijieguoAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
